.container{
    margin: auto;
    width: 90%;
    max-width: 1250px;
}
nav{
    background :#f7f7f7;
}
nav a{
    text-decoration: unset;
}
nav .container{
    text-align: center;
    padding-bottom: 16px;
}
nav .nav-brand{
    padding: 5px 0px;
    font-size: 2rem;
    font-weight: bold;
    color: black;
}
nav .nav-brand a{
    text-decoration-line: unset;
}
nav p{
    margin-bottom: 0;
}
nav .nav-menu{
    margin-top: 1rem;
    display: flex;
}
nav .nav-menu li{
    list-style-type: none;
    margin:0px 15px;
}
.jobs-found{
    padding-bottom: 1%;
    text-align: center;
    display: none;
}
.jobs-list{
    display: flex;
    flex-wrap: wrap;
}
.jobs-list .column{
    margin-bottom: 1rem;
}
.job-info{
    height: 100%;
    border: 1px solid grey;
    position: relative;
    padding: 1rem 1rem;
    margin-bottom: 1rem;
}
.job-info .apply{
    position: absolute;
    text-decoration-line: unset;
    right: 5px;
    bottom: 0px;
    background: green;
    color: white;
    padding: 5px 15px;
    border-radius: 2px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}
.job-info .company a{
    text-decoration-line: underline;
    color: black;
}
.search-form{
    display: flex;
    margin: 1rem 0rem;
    flex-wrap: wrap;
}
.search-form input, .search-form button{
    margin: 0rem 1rem;
}
#loading_start{
    margin-top: 5px;
    margin-left: 2rem;
    display: none;
    font-weight: bold;
}
.jobs-found h1{
    font-size: 25px;
}
.search-form button{
    max-width: 10rem;
}

@media(max-width: 767px){
    .search-form input, .search-form button{
        width: 100%;
        margin: 5px 5px;
    }
}

@media(max-width: 500px){
    .jobs-found h1{
        font-size: 20px;
    }
}
